import React from 'react';
import styled from 'styled-components';
import Slider from 'react-slick';
import { Title2 } from './homeStyledComponents';
import { colors } from '../styledComponents';
import Feedback from './Feedback';
import photo1 from '../../assets/photo1.jpg';
import photo2 from '../../assets/photo2.jpg';
import photo3 from '../../assets/photo3.jpg';
import photo4 from '../../assets/photo4.jpg';
import photo5 from '../../assets/photo5.jpg';

const Container = styled.div`
  padding: 20px 0px 40px;
  background-color: ${colors.lightGrey};
`;

const feedbacks = [
  {
    id: '0',
    text: 'Super contact avec daphn. Etant PMR c\'est bien pratique d\'avoir une couturire creative  domicile pour concevoir et faire une ceinture de jeans qui se met d\'une seule main et un tablier protge cuisse et genoux pour les balades en fauteuil.',
    author: 'Didier D.',
  },
  {
    id: '6',
    image: photo1,
    text: 'Merci tilli pour ce moment !',
    author: 'Sandra R',
  },
  { id: '1', text: 'De trs bons conseils, du travail de grande qualit et rapide, bravo !', author: 'Guillaume L.' },
  {
    id: '7',
    image: photo2,
    text: 'Ma super robe',
  },
  {
    id: '2',
    text: 'J\'avais un coussin d\'oreillette de casque  recoudre et 3 ourlets  faire. a a t fait en 1 semaine, une couturire trs pro est tout passe chercher et me les a ramens. Rien  redire sur le service, je recommande !',
    author: 'Marc P.',
  },
  {
    id: '8',
    image: photo5,
    author: 'Olivia P.N.',
    text: '',
  },
  {
    id: '3',
    text: 'Le concept est vraiment top et les prix trs attractifs compars aux tailleurs/retouches habituels ! Le grand plus est videmment le fait que les couturires viennent chez toi, c\'est vraiment un gain de temps trs apprciable ! Vive Tilli ! ',
    author: 'Miguel',
  },
  {
    id: '9',
    image: photo4,
    text: 'Hello',
  },
  {
    id: '4',
    text: 'Canon et c\'est jouissif de transformer des fringues qui hibernaient dans le placard !!!',
    author: 'Eleonore de R.',
  },
  {
    id: '10',
    image: photo3,
    text: 'Hello',
  },
  {
    id: '5',
    text: 'Brilliant service, pratique, rapide, top professionnal quality, quipe plus que comptente et sympa. Bravo Tilli !!!!! 20/20 !!!',
    author: 'Maria O.',
  },
];

const settings = {
  dots: true,
  infinite: true,
  slidesToShow: 4,
  slidesToScroll: 2,
  autoplay: true,
  autoplaySpeed: 3000,
  adaptiveHeight: true,
};

const TheyLoveTilli = () => (
  <Container>
    <Title2> ils ont aim </Title2>
    <Slider {...settings}>
      {feedbacks.map(feedback =>
        <Feedback
          dataIndex={feedback.id}
          key={feedback.id}
          {...feedback}
        />,
      )}
    </Slider>
  </Container>
);

export default TheyLoveTilli;



